<template>
  <div>

    <!-- ***** Contact Area Start ***** -->
    <div class="dorne-contact-area d-md-flex container" style="padding-top: 43px" id="contact">
      <!-- -->
      <div style="width: 20%">
        <div class="leftsidebar" style="width: 20%; padding-top: 30%">
          <button type="button" id="mypage_button" style="max-width: 20px; color: red" @click="Mypage_Link">
            
          </button>
          <button type="button" id="mypage_button" style="max-width: 20px" @click="My_reservation_Link">
            
          </button>
          <button type="button" id="mypage_button" style="max-width: 20px" @click="My_Delete_Link">
             
          </button>
        </div>
      </div>
      <!--     -->
      <form id="app3" @submit="checkForm" action="/mypage" method="post" novalidate="true">
        <div class="contact-form-area equal-height container" style="padding-top: 6%; width: 220%;">
          <div class="contact-text">
            <h1>  </h1>

            <div style="position: relative; left: 30%">
              <div class="field2" id="sign">
                <b label for="nickname"></b><br />
                <input type="text" id="nickname" role="textbox" placeholder="  ." name="nickname"
                  v-model="nickname" />
                <input type="button" id="crosscheck" value=" " @click="validateNickname"><br />
              </div>
              <br>
              <div class="field2">
                <b label for="password"></b><br />
                <input class="userpw" id="password" type="password" role="textbox" placeholder="  ."
                  name="password" v-model="password" /><br /><br />
              </div>
              <div class="field2">
                <b label for="passwordCheck"> </b><br />
                <input class="userpw-confirm" id="passwordCheck" type="password" role="textbox" name="passwordCheck"
                  v-model="passwordCheck" placeholder="  ." /><br /><br />
              </div>
              <!-- 5. _ -->
              <div class="field2 tel-number">
                <b label for="phone">(-)</b>
                <div>
                  <input id="phone1" type="text" role="textbox" name="phone" placeholder="ex)00012345678"
                    v-model="phone" /><br /><br />
                </div>
              </div>

              <div class="field2">
                <b label for="address"></b><br />
                <input type="text" id="zonecode" placeholder="" role="textbox" v-model="zonecode" readonly />
                <input type="button" @click="openPostcode" value=" " id="postcode"><br />
                <input type="text" placeholder="" role="textbox" v-model="roadAddress" readonly
                  style="width: 43%;" /><br />
                <input type="text" id="addressdetail" placeholder="" role="textbox" v-model="detailAddress"
                  style="width: 43%;" /><br />
              </div>

              <p v-if="errors.length">
                <br>
                <b>  </b>
              <ul>
                <li v-for="error in errors" v-bind:key="error" style="color: red;">{{ error }}</li>
              </ul>
              </p>

              <div style="position: relative; left: 125px; top: 30px">
                <input type="submit" id="addallow" value="">
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div class="main" style="width: 20%"></div>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  el: "#app3",
  data() {
    return {
      errors: [],
      phone: null,
      nickname: null,
      password: null,
      passwordCheck: null,
      zonecode: null,
      roadAddress: null,
      detailAddress: null
    };
  },
  methods: {
    Mypage_Link() {
      this.$router.push({ path: "/mypage" });
    },
    My_reservation_Link() {
      this.$router.push({ path: "/my_reservation" });
    },
    mypage_pw_check_Link() {
      this.$router.push({ path: "/mypage_pw_check" });
    },
    My_Delete_Link() {
      this.$router.push({ path: "/my_delete" });
    },

    checkForm(e) {
      e.preventDefault();
      this.errors = [];

      if (!this.nickname) {
        this.errors.push(" .");
      }

      if (!this.password) {
        this.errors.push(" 4~12 ");
      } else if (!this.validPassword(this.password)) {
        this.errors.push("  .");
      }

      if (!this.passwordCheck) {
        this.errors.push(" ");
      } else if (!this.validPasswordCheck(this.passwordCheck)) {
        this.errors.push(" ")
      }

      if (!this.phone) {
        this.errors.push(" ");
      } else if (!this.validPhone(this.phone)) {
        this.errors.push("  .");
      }

      if (!this.roadAddress) {
        this.errors.push(" ");
      }

      if (!this.errors.length) {
        this.submitForm();
      }
    },


    validPassword(password) {//     
      var re3 = /^[A-Za-z0-9]{4,12}$/
      return re3.test(password);
    },


    validPasswordCheck: function () {//   
      let pw = document.getElementById("password").value
      let pwcheck = document.getElementById("passwordCheck").value

      if (pw !== pwcheck) {
        alert(" ")
      } else {
        return true;
      }
    },


    validNickname: function (nickname) {//   
      var re0 = /^[A-Za-z-]{1,6}$/
      return re0.test(nickname);
    },


    validPhone(phone) {//    
      var re2 = /^01([0|1|6|7|8|9]?)?([0-9]{3,4})?([0-9]{4})$/
      return re2.test(phone);
    },


    submitForm() {//  .
      const formData = {
        nickname: this.nickname,
        password: this.password,
        phone: this.phone,
        address1: this.roadAddress,
        address2: this.detailAddress
      };


      axios.post('/my_update', formData)//    .
        .then(response => {
          if (response.data.message) {
            alert(response.data.message);
            //      
            location.href = "/mypage";

          } else {
            alert('  .');
            console.log(formData);
          }
        })
        .catch(error => {
          console.error(' ', error);
          alert(' .     ');
        });
    },

    validateEmail() {
      const email = this.email;
      if (!email) {
        alert("  ");
        return;
      }

      axios.post('/checkEmail', { email })
        .then(response => {
          if (response.data.exists) {
            alert("  .");
          } else {
            alert("  .");
          }
        })
        .catch(error => {
          console.error('   ', error);
          alert('   .');
        });
    },


    validateNickname() {
      const nickname = this.nickname;
      if (!nickname) {
        alert("  ");
        return;
      }

      axios.post('/checkNickname', { nickname })
        .then(response => {
          if (response.data.exists) {
            alert("  .");
          } else {
            alert("  .");
          }
        })
        .catch(error => {
          console.error('   ', error);
          alert('   .');
        });
    },


    openPostcode() {// 
      new window.daum.Postcode({
        oncomplete: (data) => {
          this.zonecode = data.zonecode;
          this.roadAddress = data.roadAddress;
        },
      }).open();
    },


  },
};
</script>
<style>
@import "Mypage.css";
</style>