<template>
  <body>
    <div class="limiter">
      <div class="container-login100">
        <div class="wrap-login100 p-l-55 p-r-55 p-t-65 p-b-54">
          <form class="login100-form validate-form">
            <div
              class="wrap-input100 validate-input m-b-23"
              data-validate="Username is required"
            >
              <span class="label-input100">()</span>
              <input
                class="input100"
                type="email"
                name="username"
                placeholder=" "
                v-model="saveData.userId"
                required
              />
            </div>

            <div
              class="wrap-input100 validate-input"
              data-validate="Password is required"
            >
              <span class="label-input100"></span>
              <input
                class="input100"
                type="password"
                name="pass"
                placeholder=" "
                v-model="saveData.userPw"
                required
              />
            </div>

            <div class="text-right p-t-8 p-b-31">
              <a href="#" style="text-decoration: none">
                <RouterLink to="Find_IdPw">
                  <p style="text-align: right" link="">/ </p>
                </RouterLink>
              </a>
            </div>

            <div class="container-login100-form-btn">
              <div class="wrap-login100-form-btn">
                <div class="login100-form-bgbtn"></div>

                <button class="login100-form-btn" @click="loginSubmit">
                  <a style="text-decoration: none" href="#">
                    <p
                      style="margin-top: 15px; color: white; text-align: center"
                    >
                      <b></b>
                    </p>
                  </a>
                </button>
              </div>
            </div>

            <div class="flex-c-m">
              <!--    api       -->
              <br />
              <!--   -->
              <div
                id="naverIdLogin"
                style="margin-right: 20px; margin-left: 2px"
              >
                <a
                  href="#"
                  class="login100-social-item bg2"
                  style="text-decoration: none"
                >
                </a>
              </div>
              <!--   -->
              <a href="#" @click="kakaoLogin()">
                <img style="width: 225px" src="./kakao_login2.png" alt="" />
              </a>
            </div>
            <div class="flex-col-c p-t-155" style="text-align: right">
              <span class="txt1 p-b-17">
                    ?
              </span>

              <a href="#" class="txt2">
                <p link="" style="text-align: right">
                  <RouterLink to="/Signup"><b></b></RouterLink>
                </p>
              </a>
            </div>
          </form>
        </div>
      </div>
    </div>
  </body>
</template>

<style src="./Login.css"></style>

<style>
#naverIdLogin {
  width: 250px;
  height: 70px;
  object-fit: cover;
  float: left;
}
</style>

<script>
import axios from "axios";

export default {
  // 
  name: "naverLogin",
  data() {
    return {
      saveData: {
        userId: "",
        userPw: "",
      },
      naverLogin: null,
    };
  },
  mounted() {
    this.naverLogin = new window.naver.LoginWithNaverId({
      clientId: "8NWAD60RgqNbWdmXePhg", //   
      callbackUrl: "http://localhost:8080/login", //  url
      isPopup: true, //  
      loginButton: {
        color: "green",
        height: 40,
        type: 3,
      },
    });

    //    
    this.naverLogin.init();

    this.naverLogin.getLoginStatus((status) => {
      if (status) {
        console.log(status);
        console.log(this.naver.user);

        //    ,callback  .
        var email = this.naverLogin.user.getEmail();
        if (email == undefined || email == null) {
          alert("  .   .");
          //        
          this.naverLogin.reprompt();
          return;
        }
      } else {
        // console.log("callback  .");
      }
    });
  },
  //     api   .
  //      . ,axios   ,cors  . vue.config.js   .
  naverLogout() {
    const accessToken = this.naverLogin.accessToken.accessToken;
    const url = `/oauth2.0/token?grant_type=delete%client_id={8NWAD60RgqNbWdmXePhg}&client_secret={bnEH07OqTy}&access_token=${accessToken}&service_provider=NAVER`;
    //https://nid.naver.com//oauth2.0/token?grant_type=delete%client_id={  }&client_secret={  }&access_token={  }&service_provider=NAVER

    axios.get(url).then((res) => {
      console.log(res.data);
    });
  },

  computed: {
    user() {
      return this.$store.state.user; // user     user() 
    },
  },

  methods: {
    loginSubmit() {
      let saveData = {};
      saveData.userId = this.userId;
      saveData.userPw = this.userPw;

      try {
        this.$axios
          .post("/api/login", JSON.stringify(saveData), {
            headers: {
              "Content-Type": `application/json`,
            },
          })
          .then((res) => {
            if (res.status === 200) {
              //    
              this.$store.commit("login", res.data);
              this.$router.push("/");
              this.$store.state.isLogin = true
              alert(" !");
            }
          });
      } catch (error) {
        console.error(error);
      }
    },

    //   
    kakaoLogin() {
      window.Kakao.Auth.login({
        scope: "profile_image, account_email",
        success: this.getKakaoAccount,
      });
    },
    //    
    getKakaoAccount(authObj) {
      // authObj   
      console.log(authObj);
      window.Kakao.API.request({
        //   
        url: "/v2/user/me",
        success: res => {
          //   
          const kakao_account = res.kakao_account; //     
          console.log(kakao_account); //   
          this.kakao_login(kakao_account); //   kakao_login 
          this.$store.commit("user", kakao_account); // store   
          this.$router.push({path:'/'}); //  
          alert(" !");
          //  
          this.$store.state.isLogin = true //    
        },
      });
    },
    async kakao_login(kakao_account) {
      // login  signup
      await this.$api("/api/kakaoLogin", {
        //   kakaoLogin sql 
        param: [
          { email: kakao_account.email, name: kakao_account.profile.nickname },
          { name: kakao_account.profile.nickname },
          ]
      });
      this.$store.commit("user", kakao_account); // vuex ,  store user  
    },
    kakaoLogout() {
      window.Kakao.Auth.logout((response) => {
        console.log(response);
        this.$store.commit("user", {}); // store  user  data null 
        alert("");
        this.$router.push({path:'/'}); //     
      });
    }
  },
};
</script>

<style>
div {
  text-align: left;
}
</style>
