<template>
    <body>
      <form id="app2" @submit="checkForm" action="/signup" method="post" novalidate="true">
        <div class="contact-form-area equal-height container" style="padding-top: 6%; max-width: 465px;">
          <div class="contact-text">
            <h1 class="title"> </h1>
  
            <div class="field2" id="sign">
              <b label for="email">()</b><br>
              <input id="email" type="text" role="textbox" placeholder="  ." name="email" v-model="email" />
              <input type="button" id="crosscheck" value=" " @click="validateEmail">
              <br /><br />
            </div>
  
            <div class="field2" id="sign">
              <b label for="nickname"></b><br />
              <input type="text" id="nickname" role="textbox" placeholder="  ." name="nickname"
                v-model="nickname" />
              <input type="button" id="crosscheck" value=" " @click="validateNickname"><br /><br />
            </div>
  
            <div class="field2">
              <b label for="password"></b><br />
              <input class="userpw" id="password" type="password" role="textbox" placeholder="  ."
                name="password" v-model="password" /><br /><br />
            </div>
  
            <div class="field2">
              <b label for="passwordCheck"> </b><br />
              <input class="userpw-confirm" id="passwordCheck" type="password" role="textbox" name="passwordCheck"
                v-model="passwordCheck" placeholder="  ." /><br /><br />
            </div>
  
            <div class="field2 tel-number">
              <b label for="phone">(-)</b>
              <div>
                <input id="phone1" type="text" role="textbox" name="phone" placeholder="ex)00012345678"
                  v-model="phone" /><br /><br />
              </div>
            </div>
  
            <div class="field2">
              <b label for="address"></b><br />
              <input type="text" id="zonecode" placeholder="" role="textbox" v-model="zonecode" readonly />
              <input type="button" @click="openPostcode" value=" " id="postcode"><br />
              <input type="text" placeholder="" role="textbox" v-model="roadAddress" readonly style="width: 67%;" /><br />
              <input type="text" id="addressdetail" placeholder="" role="textbox" v-model="detailAddress"
                style="width: 67%;" /><br />
            </div>
  
            <p v-if="errors.length">
              <br>
              <b>  </b>
            <ul>
              <li v-for="error in errors" v-bind:key="error" style="color: red;">{{ error }}</li>
            </ul>
            </p>
  
            <div style="position: relative; left: 125px; top: 30px" class="signUp">
              <input type="submit" id="addallow" value="">
            </div>
  
          </div>
        </div>
  
        <div class="main" style="width: 20%"></div>
      </form>
    </body>
  </template>
    
  <script>
  import axios from 'axios';
  
  export default {
  
  
    el: '#app2',
    data() {//  .
      return {
        errors: [],
        email: null,
        phone: null,
        nickname: null,
        password: null,
        passwordCheck: null,
        zonecode: null,
        roadAddress: null,
        detailAddress: null
      }
    },
  
  
    methods: {
  
  
      checkForm(e) {//  
        e.preventDefault();
        this.errors = [];
  
        if (!this.email) {
          this.errors.push(" .");
        } else if (!this.validEmail(this.email)) {
          this.errors.push("  .");
        }
  
        if (!this.nickname) {
          this.errors.push(" .");
        }
  
        if (!this.password) {
          this.errors.push(" 4~12 ");
        } else if (!this.validPassword(this.password)) {
          this.errors.push("  .");
        }
  
        if (!this.passwordCheck) {
          this.errors.push(" ");
        } else if (!this.validPasswordCheck(this.passwordCheck)) {
          this.errors.push(" ")
        }
  
        if (!this.phone) {
          this.errors.push(" ");
        } else if (!this.validPhone(this.phone)) {
          this.errors.push("  .");
        }
  
        if (!this.roadAddress) {
          this.errors.push(" ");
        }
  
        if (this.errors.length === 0) {
          this.submitForm();
        }
      },
  
  
      validEmail(email) {//     
        var re1 = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
        return re1.test(email);
      },
  
  
      validPassword(password) {//     
        var re3 = /^[A-Za-z0-9]{4,12}$/
        return re3.test(password);
      },
  
  
      validPasswordCheck: function () {//   
        let pw = document.getElementById("password").value
        let pwcheck = document.getElementById("passwordCheck").value
  
        if (pw !== pwcheck) {
          alert(" ")
        } else {
          return true;
        }
      },
  
  
      validNickname: function (nickname) {//   
        var re0 = /^[A-Za-z-]{1,6}$/
        return re0.test(nickname);
      },
  
  
      validPhone(phone) {//    
        var re2 = /^01([0|1|6|7|8|9]?)?([0-9]{3,4})?([0-9]{4})$/
        return re2.test(phone);
      },
  
  
      submitForm() {//  .
        const formData = {
          email: this.email,
          nickname: this.nickname,
          password: this.password,
          phone: this.phone,
          address1: this.roadAddress,
          address2: this.detailAddress
        };
  
  
        axios.post('/signup', formData)//    .
          .then(response => {
            if (response.data.message) {
              alert(response.data.message);
              //      
              location.href = "/login";
            } else {
              alert('  .');
              console.log(formData);
            }
          })
          .catch(error => {
            console.error(' ', error);
            alert(' .     ');
          });
      },
  
      validateEmail() {
        const email = this.email;
        if (!email) {
          alert("  ");
          return;
        }
  
        axios.post('/checkEmail', { email })
          .then(response => {
            if (response.data.exists) {
              alert("  .");
            } else {
              alert("  .");
            }
          })
          .catch(error => {
            console.error('   ', error);
            alert('   .');
          });
      },
  
  
      validateNickname() {
        const nickname = this.nickname;
        if (!nickname) {
        alert("  ");
          return;
        }
  
        axios.post('/checkNickname', { nickname })
          .then(response => {
            if (response.data.exists) {
              alert("  .");
            } else {
              alert("  .");
            }
          })
          .catch(error => {
            console.error('   ', error);
            alert('   .');
          });
      },
  
  
      openPostcode() {// 
        new window.daum.Postcode({
          oncomplete: (data) => {
            this.zonecode = data.zonecode;
            this.roadAddress = data.roadAddress;
          },
        }).open();
      },
  
  
    },
  }
  </script>
    
  <style>
  @import "Signup.css";
  
  .error {
    font-size: 1px;
    height: 20px;
    color: red;
    font-weight: 700;
  }
  </style>
    