// Vuex            .       
// Store         Store  
import { createStore } from "vuex";
import router from './router'
//       
//vuex state     
import persistedstate from "vuex-persistedstate";

const store = createStore({
  state() {
    //    . State   Component computed  .   : this.$store.state.items
    return {
      user: null,
      userId: '',
      userPw: '',
      token: '',
      isLogin: false,
      isLoginError: false
    };
  },
  mutations: {
    //Mutation  State . . Mutations   Component Methods  .   : this.$store.commit('/')
    user(state, data) {
      state.user = data;
    },
    loginSuccess(state) {
      state.isLogin = true
      state.isLoginError = false
    },
    loginError(state) {
      state.isLogin = false
      state.isLoginError = true
    },
    //        
    login: function (state, payload) {
      state.userId = payload.userId
      state.userPw = payload.userPw
      state.token = payload.token
    },
    loginCheck: function (state) {
      if (!state.token) {
        router.push({
          name: 'login'
        }).catch(error => {
          console.log(error)
        })
      }
    }
  },
  plugins: [
    persistedstate({
      paths: ['user']
    })
  ]
});

export default store;