<template>
  <html>

  <body>
    <form id="app2" @submit="checkForm" action="/something" method="post" novalidate="true">
      <!--     -->
      <div class="contact-form-area" style="padding-top: 6%; height: 100px; margin: auto;">
        <div class="contact-text">
          <h1>  </h1>
          <div style="float: left; margin-top: 10%; margin-left: 20%; ">

            <div class="field2" id="sign">
              <b label for="name"> </b>
              <br />
              <input type="text" id="name" role="textbox" placeholder="  " name="name"
                v-model="name" /><br /><br />

            </div>

            <!-- 5. _ -->
            <div class="field2 tel-number">
              <b label for="phone">(-)</b>
              <div>
                <input id="phone1" type="text" role="textbox" name="phone" placeholder="ex)00012345678"
                  v-model="phone" /><br /><br />
              </div>
            </div>

            <div class="field2" id="sign">
              <b label for="email">()</b><br>
              <input id="email" type="text" role="textbox" placeholder="  ." name="email"
                v-model="email" /><br /><br />
            </div>

            <div class="field2" id="sign">
              <b> </b>&nbsp;&nbsp;
              <select v-model="capacity" class="capacity" style="width: 70px;">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
              </select>
            </div>

            <p v-if="errors.length">
              <br>
              <b>  </b>
            <ul>
              <li v-for="error in errors" v-bind:key="error" style="color: red;">{{ error }}</li>
            </ul>
            </p>

            <div style="position: relative; left: 125px; top: 30px">
              <!-- <input type="submit" id="addallow" value="" @click="PaymentInsert"> -->
              <input type="submit" v-on:click="requestPay()" id="addallow" value="">

            </div>
          </div>
        </div>
      </div>

      <!-- -->
      <div class="right" style="margin-right: 15%;">
        <section class="info" data-v-33033856="">
          <p class="name" data-v-33033856="">
            <strong data-v-33033856=""></strong>
            {{ GetAcco.ACCO_NAME }}
          </p>
          <p data-v-33033856=""><strong data-v-33033856=""></strong>{{ GetAcco.RESERVATION_CHECK_IN }}
          </p>
          <p data-v-33033856=""><strong data-v-33033856=""></strong>{{ GetAcco.RESERVATION_CHECK_OUT }}
          </p>
        </section>
        <section class="total_price_pc" data-v-33033856="">
          <p data-v-33033856="">
            <strong data-v-33033856="">
              <b data-v-33033856="">  </b>(VAT)</strong>
            <span class="in_price" data-v-33033856="">{{ getCurrencyFormat(GetAcco.ACCO_PRICE) }}</span>
          </p>
          <ul data-v-33033856="">
            <li data-v-33033856="">  ,   </li>
            <li data-v-33033856="">
                <span data-v-33033856=""> </span> 
              <span data-v-33033856=""></span>  
            </li>
          </ul>
        </section> <!---->
      </div>
    </form>
  </body>

  </html>
</template>


<script>
import axios from 'axios';

var IMP = window.IMP;   //  
IMP.init("imp56476234");

export default {
  el: "#app2",
  data() {
    return {
      capacity: "2",
      errors: [],
      name: null,
      email: null,
      phone: null,
      total: 1,
      totalPrice: 0,
      RESERVATION_ID: this.$route.query.RESERVATION_ID,
      GetAcco: {},
      accoid: this.$route.query.ACCO_ID, // 
      userno: this.$route.query.USER_NO,
      check_in: '2012-1-12', // 
      check_out: '2012-3-14' // 

    }
  },
  created() {
    this.ACCO_ID = this.$route.query.ACCO_ID;
    this.RESERVATION_ID = this.$route.query.RESERVATION_ID;
    this.Acco();

  },
  methods: {
    async Acco() {
      let GetAcco = await this.$api("/api/GetReservation", { param: [this.ACCO_ID] });
      this.GetAcco = GetAcco[0];

      console.log(this.GetAcco);
    },

    checkForm(e) {
      e.preventDefault();
      this.errors = [];
      if (!this.name) {
        this.errors.push(" .");
      }

      if (!this.phone) {
        this.errors.push(" ");
      } else if (!this.validPhone(this.phone)) {
        this.errors.push("  .");
      }

      if (!this.email) {
        this.errors.push(" .");
      } else if (!this.validEmail(this.email)) {
        this.errors.push("  .");
      }

      if (!this.errors.length) return true;
    },


    validEmail: function (email) {
      var re1 = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      return re1.test(email);
    },

    validPhone: function (phone) {
      var re2 = /^01([0|1|6|7|8|9]?)?([0-9]{3,4})?([0-9]{4})$/
      return re2.test(phone);
    },


    


    getCurrencyFormat(value) {
      return this.$currencyFormat(value);
    },


    requestPay() {
            if (this.name == null || this.phone == null || this.email === null || !this.validEmail(this.email) || !this.validPhone(this.phone)) {
        return
      }
      IMP.request_pay({ // param
        pg: "inicis",
        pay_method: "card",
        merchant_uid: "335eggwe4-86gdsffyui",
        name: this.GetAcco.ACCO_NAME,
        amount: this.GetAcco.ACCO_PRICE,
        buyer_email: this.email,
        buyer_name: this.name,

      }, rsp => { // callback
        if (rsp.success) {
          this.submitForm();

          location.href = "/reservation_info"
          //    ,

        } else {
          this.$router.go(-3);
          alert(" ");


          //    ,

        }
      });
    },

    submitForm() {//  .
      const formData = {
        totalprice: this.GetAcco.ACCO_PRICE,
        reservationid: this.GetAcco.RESERVATION_ID,
        reservation_status: "",
        check_in: this.check_in,
        check_out: this.check_out,
        userno: this.userno,
        accoid: this.accoid,
        RESERVATION_ID: this.RESERVATION_ID,
        ACCO_NAME: this.ACCO_NAME
      };



      axios.post('/payment', formData)//    .
        .then(response => {
          if (response.data.message) {
            alert(response.data.message);
            
            
          } else {
            alert(' .');
            console.log(formData);
          }
        })
        .catch(error => {
          console.error(' ', error);
          alert(' ');
        });
    },


  }
}

</script>

<style>
@import "Payment.css";

body,
h2,
p,
div,
strong,
span,
input,
a {
  text-align: left;
}

.error {
  font-size: 1px;
  height: 20px;
  color: red;
  font-weight: 700;
}
</style>