<template>
  <div>
    <!-- ***** Contact Area Start ***** -->
    <div
      class="dorne-contact-area d-md-flex container"
      style="padding-top: 43px"
      id="contact"
    >
      <!-- -->
      <div style="width: 20%">
        <div class="leftsidebar" style="width: 20%; padding-top: 30%">
          <button
            type="button"
            id="mypage_button"
            style="max-width: 20px"
            @click="Mypage_Link"
          >
            
          </button>
          <button
            type="button"
            id="mypage_button"
            style="max-width: 20px; color: red"
            @click="My_reservation_Link"
          >
            
          </button>
          <button
            type="button"
            id="mypage_button"
            style="max-width: 20px"
            @click="My_Delete_Link"
          >
             
          </button>
        </div>
      </div>
      <!--     -->
      <div
        class="contact-form-area equal-height container"
        style="padding-top: 6%"
      >
        <div class="contact-text">
          <h1></h1>
          <div v-if="reslist == null" class="reslistempty">
            <div class="really2">
                <br />  
            </div>
            <br />
            <button type="button" id="main" @click="main_link">
                 
            </button>
          </div>

          <div
            v-else
            style="position: relative; left: 18%"
            v-for="(rs, i) in reslist"
            :key="i"
          >
            <div class="res-content" style="height: 350px">
              <img
                :src="`/download/${rs.ACCO_ID}/${rs.ACCO_IMAGE}`"
                class="room-image"
                style="display: block; margin-bottom: 0"
              />
              <strong style="font-size: 18px">{{ rs.ACCO_NAME }}</strong>
              <div style="position: relative; left: 69px; margin: 10px">
                <button
                  id="button2"
                  style="display: inline-block"
                  @click="openModal(rs)"
                >
                  
                </button>
                <button
                  id="button3"
                  @click="showConfirmation()"
                  style="display: inline-block"
                >
                  
                </button>
              </div>
              <div
                id="popup"
                class="popup"
                style="display: none; left: 30px; top: 50px"
              >
                <p class="popup-message" style="font-size: 18px">
                   ?
                </p>
                <div class="popup-buttons">
                  <button id="button7" @click="hideConfirmation()"></button>
                </div>
                <div class="popup-buttons">
                  <button id="button8" @click="cancelReservation(rs)">
                    
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div id="modal" class="modal">
          <div class="modal-content" v-for="(resm, i) in resmodalList" :key="i">
            <div class="reslistpopup">
              <h2> </h2>
              <div>
                <strong> : {{ resm.ACCO_NAME }}</strong>
              </div>
              <section>
                <div>
                  <p><strong></strong> {{ resm.RESERVATION_CHECK_IN }}</p>
                  <p>
                    <strong></strong>
                    {{ resm.RESERVATION_CHECK_OUT }}
                  </p>
                </div>
                <div>
                  <p><strong></strong> {{ resm.RESERVATION_ID }}</p>
                  <p><strong>  </strong> {{ resm.USER_TEL }}</p>
                </div>
              </section>
              <div class="total">
                <div class="payment-info">
                  <div class="payment-info-top">
                    <p><strong>  </strong></p>
                  </div>
                  <div class="payment-info-middle">
                    <p>
                      <b>{{ resm.PAYMENT_TOTAL_PRICE }}</b>
                    </p>
                  </div>
                </div>
              </div>
              <section>
                <p>
                  <input type="button" value="" @click="closeModal()" />
                </p>
              </section>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
axios.defaults.baseURL = "http://localhost:3000"; // 
axios.defaults.headers.post["Content-Type"] = "application/json;charset=utf-8";
axios.defaults.headers.post["Access-Control-Allow-Origin"] = "*";

export default {
  data() {
    return {
      USER_NO: 1, //     USER_NO 
      ACCO_ID: "",
      reslist: [],
      resmodalList: [],
    };
  },
  mounted() {
    //   
    this.Get_Reservation_Info();
  },

  methods: {
    //
    Mypage_Link() {
      this.$router.push({ path: "/mypage" });
    },
    My_reservation_Link() {
      this.$router.push({ path: "/my_reservation" });
    },
    mypage_pw_check_Link() {
      this.$router.push({ path: "/mypage_pw_check" });
    },
    My_Delete_Link() {
      this.$router.push({ path: "/my_delete" });
    },
    main_link() {
      this.$router.push({ path: "/" });
    },

    // 
    async Get_Reservation_Info() {
      this.reslist = await this.$api("/api/reslist", {
        param: [this.USER_NO], //  ,  USER_NO  
      });
    },

    //   
    async Get_Modal_Info() {
      this.resmodalList = await this.$api("/api/resmodalList", {
        param: [this.ACCO_ID],
      });
    },

    // 
    async Delete_res() {
      this.ResDelete = await this.$api("/api/ResDelete", {
        param: [this.ACCO_ID],
      });
    },

    //
    showConfirmation() {
      var popup = document.getElementById("popup");
      popup.style.display = "block";
    },
    hideConfirmation() {
      var popup = document.getElementById("popup");
      popup.style.display = "none";
    },
    cancelReservation(rs) {
      var popup = document.getElementById("popup");
      popup.style.display = "none";
      this.ResDelete = this.$api("/api/ResDelete", { param: [rs.ACCO_ID] });
      alert(" ");
      this.hideConfirmation();
      location.href = "/My_Reservation";
    },

    // 
    openModal(rs) {
      var modal = document.getElementById("modal");
      this.ACCO_ID = rs.ACCO_ID;
      this.Get_Modal_Info();
      modal.style.display = "block";
    },

    closeModal() {
      var modal = document.getElementById("modal");
      console.log("  ");
      modal.style.display = "none";
    },
  },
};
</script>
<style>
@import "Mypage.css";
</style>
